import React from "react";
import {Field, Form, Formik} from "formik";


const ProfileDataForm =({deactivateEdit,saveProfile}) => {
    return <div>
        <Formik
            initialValues={{
                aboutMe: '',
                lookingForAJob: true,
                fullName: 'tffriend025',
                lookingForAJobDescription: ''
            }}


            onSubmit={async (values) => {
                saveProfile(values)
                deactivateEdit()
            }}>
            {({values}) => (
                <Form>
                    <label htmlFor="aboutMe"> aboutMe: </label>
                    <Field name="aboutMe" type="text"/>
                    <br></br>
                    <label htmlFor="lookingForAJobDescription">Looking for a job: </label>
                    <Field name="lookingForAJobDescription" type="text"/>
                    <br></br>


                    <button type={"submit"}> Сохранить</button>

                </Form>
            )}
        </Formik>
    </div>
}

export default ProfileDataForm
