import React from "react";
import Profile from "./Profile";
import {connect} from "react-redux";
import {
    getStatusThunk,
    profileThunk,
    saveAvatar,
    saveProfile,
    setUserProfile,
    updateStatusThunk
} from "../../redux/profileReducer";
import {withRouter} from "react-router-dom";
import {compose} from "redux";
import {withAuthRedirect} from "../HOC/withAuthRedirect";


class ProfileContainer extends React.Component {


    refreshProfile() {
        let {profileThunk, getStatusThunk} = this.props
        let userId = this.props.match.params.userId;
        if (!userId) {
            userId = this.props.authorizedUserId;
            if (!userId) {
                this.props.history.push('/login')
            }
        }
        profileThunk(userId);
        getStatusThunk(userId);


    }


    componentDidMount() {
        this.refreshProfile()
    }

    render() {
        return <>
            <Profile userPage={this.props.userPage}
                     userId={this.userId}
                     status={this.props.statusText}
                     setStatus={this.props.updateStatusThunk}
                     isOwner={!this.props.match.params.userId}
                     saveAvatar={this.props.saveAvatar}
                     saveProfile={this.props.saveProfile}
            />
        </>
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.match.params.userId != prevProps.match.params.userId) {
            this.refreshProfile()
        }

    }
}

let mapStateToProps = (state) => {
    return {
        userPage: state.profilePage.userPage,
        statusText: state.profilePage.statusText,
        authorizedUserId: state.auth.id,
        updateProfileSuccess: state.profilePage.updateProfileSuccess
    }
};


export default compose(withAuthRedirect,
    withRouter,
    connect(mapStateToProps,
        {setUserProfile, profileThunk, getStatusThunk,updateStatusThunk,saveAvatar,saveProfile}))(ProfileContainer);
