import React, {useState} from "react";
import s from './../Profile.module.css';
import userpng from '../../../assets/images/userpng.jpg'
import StatusWithHook from '../StatusHook'
import Preloader from "../../Common/Preloader";
import ProfileDataForm from "./ProfileDataForm";


const ProfileInfo = ({saveProfile,userPage,setStatus,status, isOwner,saveAvatar}) => {

    let [editMode, setEditMode] = useState(false);
    if (!userPage) {
        return <Preloader/>
    }

    const onAvatarChanged = (e) =>{
        if (e.target.files.length) {
            saveAvatar(e.target.files[0])
        }

    }



    const activateEdit = () => {
        setEditMode(true)
    }

    const deactivateEdit = () => {
        setEditMode(false)

    }


    return (
            <div className={s.info}>
                <div className={s.avatar1}>
                    <img src={userPage.photos.large || userpng} className={s.avatar}/>

                    {isOwner &&
                    <div className={s.avatarChanger}><input type={"file"} className={s.setPhotoHide} id='updateUserAvatar' onChange={onAvatarChanged} />
                        <label htmlFor="updateUserAvatar" className={s.setPhoto}>Обновить фото</label></div> }
                </div>
                <div className={s.article}>
                    <h1 className={s.Name}>{userPage.fullName} </h1>
                    <div className={s.about}><StatusWithHook status={status} setStatus={setStatus}/> </div>

                    { editMode ? <ProfileDataForm saveProfile={saveProfile} deactivateEdit={deactivateEdit} /> :
                    <ProfileData userPage={userPage} isOwner={isOwner} activateEdit={activateEdit}/> }
                </div>
        </div>
    );
}

const ProfileData =({userPage, isOwner,activateEdit}) => {
  return <>
      {isOwner && <div><button onClick={activateEdit}>редактировать</button></div>}
      <p className={s.about}> About me: {userPage.aboutMe ? userPage.aboutMe : 'я  просто ноль' }</p>
        <p className={s.about}> Looking for a job: {userPage.lookingForAJob ? userPage.lookingForAJobDescription : 'not looking'}
        </p>
        <p className={s.contacts}>{Object.keys(userPage.contacts).map(key => {
                return <Contact contactTitle={key} contactValue={userPage.contacts[key] } />
            }
        )}
        </p>
        <p className={s.about}> ID: {userPage.userId}
        </p>
</>
}


const Contact = ({contactTitle, contactValue}) => {

    return <div><b>{contactTitle}</b>: {contactValue}</div>
}

export default ProfileInfo;
